@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Roboto);
@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Roboto);
body {
  background: #e6ebe0;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  color: rgb(53, 53, 53);
}

.title {
  font-size: 7em;
  text-align: center;
  font-family: "Bebas Neue", sans-serif;
  color: #2c2c54;
}

h3 {
  padding-top: 0.25em;
}

.vertical-timeline-element-content {
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25),
    0 0.4em 1.25em 0 rgba(53, 53, 53, 0.15) !important;
  padding: 2em 3em !important;
}

.date {
  color: rgb(54, 54, 54);
}

#description {
  margin: 1.5em 0 2em 0;
}

.button {
  text-decoration: none;
  padding: 0.5em 1em;
  border-radius: 5px;
  color: white;
  text-align: center;
  cursor: pointer;
}

.workButton {
  background-color: #5ca4a9;
}

.workButton:hover {
  background-color: #4f8c91;
  color: white;
}

.schoolButton {
  background-color: #d4af37;
}

.schoolButton:hover {
  background-color: rgb(175, 136, 45);
  color: white;
}

@media only screen and (max-width: 1700px) {
  .vertical-timeline-element-date {
    display: block !important;
    float: none !important;
    color: rgb(44, 44, 44);
    margin-top: 1.5em;
  }
}

#wrapper{margin-left:auto;margin-right:auto;width:960px}.buttons{width:1000px;margin-left:1vw;display:inline;margin-bottom:1.5vw;padding-top:5%}.action_btn{width:300px;margin:0 auto;display:inline}.App{top:0;left:0;width:100vw;height:100vh;font-family:"Roboto";display:flex}@media screen and (max-width: 980px){.App{flex-direction:column}}.App .Image{width:50vw;height:60vw;position:relative;background-size:cover;background-position:center}@media screen and (max-width: 980px){.App .Image{width:100vw;height:70vh}}.App .Profile{width:50vw;padding:5vw 3vw;box-sizing:border-box;display:flex;flex-direction:column;color:#2c2c54}@media screen and (max-width: 980px){.App .Profile{width:100vw;height:30vh}}.App .Profile .Name{font-size:5vw;font-family:"Bebas Neue",sans-serif;margin-bottom:3vw}.App .Profile .Bio{font-size:20px;line-height:1.8;margin-bottom:3vw;opacity:.4;margin-left:1vw}.App .Profile .Bio2{font-size:20px;line-height:1.8;margin-bottom:1vw;opacity:.4;margin-left:1vw}.App .Profile .Bio3{font-size:20px;line-height:1.8;margin-bottom:1vw;opacity:.4;margin-left:1vw}.App .Profile .Bio4{font-size:20px;line-height:1.8;margin-bottom:4vw;opacity:.4;margin-left:1vw}.App .Profile .Quote blockquote{font-size:20px;font-family:"Montserrat",sans-serif;font-style:italic;line-height:1.4;margin-bottom:.3em}.App .Profile .Quote .byline{font-family:"Times",serif;padding-left:14vw;font-size:22px;font-style:italic;opacity:.5}
.CustomIcon {
  justify-content: right;
  align-items: right;
  width: 1em;
  height: 1em;
  position: relative;
  vertical-align: middle;
}

#uioverrides h1 {
  line-height: 1;
  color: inherit;
  margin: 0;
}

.rowC{display:flex;padding-left:10%;padding-top:8%;padding-bottom:3%;width:80%;height:100%}@media screen and (max-width: 900px){.rowC{flex-direction:column}}.label{padding:10px;width:100%;height:100%}.spinner{padding-top:10%;padding-bottom:5%}
#wrapperf{margin-right:22%;margin-left:10%;width:960px}.fitaction_btn{width:300px;margin:0 auto;display:inline}.stupid{margin-left:11% !important;margin-top:2%;font-size:20px;line-height:1.8;margin-bottom:1vw}.fitApp{padding-top:2%;width:100vw;height:45vh;font-family:"Roboto";display:flex}@media screen and (max-width: 980px){.fitApp{flex-direction:column}}.fitApp .fitImage{width:50vw;height:60vw;position:relative;background-size:cover;background-position:center}@media screen and (max-width: 980px){.fitApp .fitImage{width:100vw;height:70vh}}.fitApp .fitProfile{width:70vw;padding:1vw 1vw;box-sizing:border-box;display:flex;flex-direction:column;color:#2c2c54}@media screen and (max-width: 980px){.fitApp .fitProfile{width:100vw;height:30vh}}.fitApp .fitProfile .fitName{font-size:4vw;padding-bottom:5%;font-family:"Bebas Neue",sans-serif;margin-bottom:1vw}.fitApp .fitProfile .fitBio{font-size:20px;line-height:1.8;opacity:.4}.fitApp .fitProfile .fitBio2{font-size:20px;line-height:1.8;opacity:.4}.fitApp .fitProfile .fitBio3{font-size:20px;line-height:1.8;opacity:.4}
